import { Input, Form, DatePicker, InputNumber, Row, Col, Select } from "antd";
import moment from "moment";

// ************************* Input ****************************
export function MyInput(props: any) {
    function handleChangeInput(value: any) {
        if (props.setValue !== undefined) {
            props.setValue(value.target.value);
        }
    }

    return (
        <Input
            onClick={props.onClick}
            value={props.value}
            onChange={handleChangeInput}
            required={props.required}
            readOnly={props.readOnly} />
    );
}

export function MyFormItemInput(props: any) {
    return (
        <Form.Item label={props.label}>
            <MyInput
                onClick={props.onClick}
                value={props.value}
                setValue={props.setValue}
                required={props.required}
                readOnly={props.readOnly} />
        </Form.Item>
    );
}

// *************************** Datepicker **************************
export function MyFormItemDatePicker(props: any) {
    function handleChangeOption(value: any) {
        if (props.setValue !== undefined) {
            props.setValue(value);
        }
    }

    function customeFormat(value: any) {
        return moment(value).format("DD/MM/YYYY");
    }

    return (
        <Form.Item label={props.label}>
            <DatePicker
                value={props.value}
                onChange={handleChangeOption}
                format={customeFormat}
                inputReadOnly={props.readOnly} />
        </Form.Item>
    );
}

// *************************** Datetimepicker **************************
export function MyFormItemDateTimePicker(props: any) {
    function handleChangeOption(value: any) {
        if (props.setValue !== undefined) {
            props.setValue(value);
        }
    }

    function customeFormat(value: any) {
        return moment(value).format("DD/MM/YYYY HH:mm:ss");
    }

    return (
        <Form.Item label={props.label}>
            <DatePicker
                showTime
                value={props.value}
                onChange={handleChangeOption}
                format={customeFormat}
                disabled={props.readOnly} />
        </Form.Item>
    );
}

// *************************** Number **************************
export function MyFormItemInputNumber(props: any) {
    function handleChangeInput(value: any) {
        if (props.setValue !== undefined) {
            props.setValue(value);
        }
    }

    return (
        <InputNumber
            min={props.min}
            max={props.max}
            value={props.value}
            onChange={handleChangeInput} />
    );
}

export function MyFormItemInputNumberColSpan(props: any) {
    return (
        <div>
            <Row className={props.className}>
                <Col span={props.colSpan1}>
                    <span>{props.label}</span>
                </Col>
                <Col span={props.colSpan2}>
                    <MyFormItemInputNumber
                        min={props.min}
                        max={props.max}
                        value={props.value}
                        setValue={props.setValue} />
                </Col>
            </Row>
        </div>

    );
}

// *************************** Select **************************
export function MyFormItemSelect(props: any) {
    function handleChangeOption(value: any) {
        if (props.setValue !== undefined) {
            props.setValue(value);
        }

        if (props.onChange !== undefined) {
            props.onChange(value);
        }
    }

    return (
        <Form.Item label={props.label}>
            <Select
                options={props.options}
                value={props.value}
                onChange={handleChangeOption} />
        </Form.Item>
    );
}


