import { Button } from 'antd';
// import Loading from './Loading';
import './UIStyle.css';

export function MyButton(props: any) {
    let htmlType = props.htmlType !== undefined ? props.htmlType : "button";

    function onClick() {
        if (props.onClick !== undefined) {
            props.onClick();
        }
    }

    return (
        <Button
            disabled={props.disabled}
            loading={props.isLoading}
            htmlType={htmlType}
            className={`btn_${props.type} ${props.className}`}
            onClick={onClick}>
            {/* <Loading isLoading={props.isLoading} />  */}
            {props.value}
        </Button>
    );
}